<template>
  <div class="container py-5">
    <form @submit.stop.prevent="handleSubmit">
      <div class="form-group">
        <label for="name">Name</label>
        <input
          id="name"
          type="text"
          name="name"
          class="form-control"
          placeholder="Enter Name"
          required
          v-model="profile.name"
        >
      </div>

      <div class="form-group">
        <label for="image">Image</label>
        <img
            v-if="profile.image"
            :src="profile.image"
            class="d-block img-thumbnail mb-3"
            width="200"
            height="200"
        >
        <input
          id="image"
          type="file"
          name="image"
          accept="image/*"
          class="form-control-file"
          @change="handleFileChange"
        >
      </div>

      <button
        type="submit"
        class="btn btn-primary"
        :disabled="isProcessing"
      >
        Submit
      </button>
    </form>
  </div>
</template>

<script>
import { Toast } from '../utils/helpers'
import usersAPI from '../apis/users'
import { mapState } from 'vuex'

export default {
    data() {
        return {
            profile: {
                id: -1,
                name: '',
                image: ''
            },
            isProcessing: false
        }
    },
    created() {
        const { id } = this.$route.params
        this.fetchUser(id)
    },
    methods: {
        async fetchUser(userId) {
            try {
                if(userId !== this.currentUser.id) {
                    this.$router.push({ name: 'not-found'})
                }
                const { data } = await usersAPI.get({ userId })
                const { id, name, image } = data.profile
                this.profile = {
                    id,
                    name,
                    image
                }
            } catch(err) {
                Toast.fire({
                    icon: 'error',
                    title: '無法取得使用者資料，請稍後再試'
                })
                console.log('error', err)
            }
        },
        async handleSubmit(e) {
            try {
                this.isProcessing = true
                const form = e.target
                const updateData = new FormData(form)
                const { data } = await usersAPI.update({ userId: this.profile.id, updateData })
                if(data.status !== 'success') {
                    this.isProcessing = false
                    throw new Error(data.message)
                }
                this.$router.push({ name: 'user-profile', params: this.profile.id})
                Toast.fire({
                    icon: 'success',
                    title: '個人資料更新成功'
                })
            } catch(err) {
                Toast.fire({
                    icon: 'error',
                    title: '無法更新個人資料，請稍後再試'
                })
                console.log('error', err)
                this.isProcessing = false
            }
        },
        handleFileChange(e) {
            const { files } = e.target
            if(files.length === 0) this.profile.image = ''
            else {
                const imageURL = window.URL.createObjectURL(files[0])
                this.profile.image = imageURL
            }
        }
    },
    computed: {
        ...mapState(['currentUser'])
    }
}
</script>